<template>
  <div>
    <canvas id="canvas"></canvas>
  </div>
</template>

<script>
export default {
  data(){
    return{
      drawFun:true
    }
  },
  destroyed() {
    this.drawFun = false;
  },
  mounted() {
    let that = this
    var w = window.innerWidth;
    var h = window.innerHeight;
    var canvas = document.getElementById("canvas");
    canvas.width = w;
    canvas.height = h;
    var ctx = canvas.getContext("2d");

    var text_arr = [
      "Á®ãÂ∫èÂëòÊï≤‰ª£Á†Å~",
      "Áà±ÁúãÁÆÄ‰π¶",
      "‰∏çÊÉ≥Âä†Áè≠",
      "ÊàëÊÉ≥Âä†Ëñ™ÔºÅ",
      "Bug ~ Bug ~Bug",
      "ÂñúÊ¨¢Ëä±ÈáåËÉ°Âì®",
      "Áà±ÂÜôÁâπÊïàüéâ",
      "üëçÁ≤æÁ•ûÂ∞è‰ºô!",
      "‰ºüÂ§ßÁöÑÈù¢ËØïÂÆòÔºÅ",
    ]; //ÊòæÁ§∫ÊñáÂ≠ó
    var text_num = 10; //È°µÈù¢Ë¶ÅÊòæÁ§∫ÁöÑÊñáÂ≠ó‰∏™Êï∞
    var word_arr = []; //ÂÇ®Â≠òÊñáÂ≠óÂùêÊ†áÁ≠âÂ±ûÊÄßÁöÑÊï∞ÁªÑ
    var txt_min_size = 10; //ÊñáÂ≠óÊúÄÂ∞èÂ§ßÂ∞è
    var txt_max_size = 20; //ÊñáÂ≠óÊúÄÂ§ßÂ§ßÂ∞è
    var keypress = false; //ÈªòËÆ§ÈîÆÁõòÊú™ÁÇπÂáªÁä∂ÊÄÅÔºåÁÇπÂáªÈîÆÁõòÂÆûÁé∞Âä†ÈÄüÊïàÊûú
    var acclerate = 3; //ÁÇπÂáªÈîÆÁõòÂä†ÈÄüÈÄüÁéá

    //ÁîüÊàê‰∏Ä‰∏™ÈöèÊú∫Èöè
    function random(min, max) {
      return Math.random() * (max - min + 1) + min;
    }
    //ÂÆûÁé∞‰∏çÂêåÂ§ßÂ∞èÊñáÂ≠óÁöÑÁßªÂä®ÈÄüÂ∫¶‰∏çÂêå
    //out_min ÊúÄÂ∞èÈÄüÂ∫¶
    //out_max ÊúÄÂ§ßÈÄüÂ∫¶
    function range_map(value, in_min, in_max, out_min, out_max) {
      return (
        ((value - in_min) * (out_max - out_min)) / (in_max - in_min) + out_min
      );
    }

    // ËøîÂõû‰∏Ä‰∏™Êï∞ÁöÑÂπ≥ÊñπÊ†π
    function distance(x, y, x1, y1) {
      // sqrt() ÊñπÊ≥ïÂèØËøîÂõû‰∏Ä‰∏™Êï∞ÁöÑÂπ≥ÊñπÊ†π„ÄÇ
      // a¬≤ + b¬≤ = c¬≤
      return Math.sqrt((x1 - x) * (x1 - x) + (y1 - y) * (y1 - y));
    }
    //ÂÇ®Â≠òÊñáÂ≠óÂùêÊ†á‰ΩçÁΩÆ
    function saveText() {
      for (let i = 0; i < text_num; i++) {
        let t = {
          x: random(0, w),
          y: random(0, h),
          text: text_arr[~~random(0, text_arr.length - 1)],
          size: ~~random(txt_min_size, txt_max_size)
        };
        word_arr.push(t);
      }
    }

    //Âú®canvasÁªòÂà∂ÊñáÂ≠óÁßªÂä®ÊïàÊûú
    function draw() {
      ctx.clearRect(0, 0, w, h);
      ctx.fillStyle = "#5a5a5a";
      for (let i = 0; i < word_arr.length; i++) {
        //ÊñáÂ≠óÂ§ßÂ∞èÔºåÊ†∑Âºè
        ctx.font = word_arr[i].size + "px sans-serif";
        //ÊñáÂ≠óÂÆΩÂ∫¶
        var t_w = ctx.measureText(word_arr[i].text);
        ctx.fillText(word_arr[i].text, word_arr[i].x, word_arr[i].y);
        if (keypress) {
          word_arr[i].x +=
            range_map(word_arr[i].size, txt_min_size, txt_max_size, 2, 4) *
            acclerate;
        } else {
          word_arr[i].x += range_map(
            word_arr[i].size,
            txt_min_size,
            txt_max_size,
            2,
            3
          );
        }
        //Â¶ÇÊûúÂ§ß‰∫éÁ™óÂè£ÂÆΩÂ∫¶ÔºåÈÇ£‰πàÈáçÊñ∞ËøîÂõûÔºåÂπ∂‰∏îÈöèÊú∫yËΩ¥‰ΩçÁΩÆÂíåÂ§ßÂ∞è
        if (word_arr[i].x >= w) {
          word_arr[i].x = -t_w.width * 5;
          word_arr[i].y = random(0, h);
          word_arr[i].size = ~~random(txt_min_size, txt_max_size);
        }
      }
      ctx.fill();
      if (that.drawFun) {
        window.requestAnimationFrame(draw);
      }
    }
    saveText();
    draw();
  }
};
</script>

<style lang="scss" scoped>
#canvas {
  display: block;
}
</style>


