<template>
  <div class="content">
    <div class="user">
      <div class="user_img">
        <img src="@/assets/img/user01.png" alt />
      </div>
      <div class="tab_nav" v-for="(i,index) in navList" :key="index" @click="skip(i.name)">
        <span class="nav">{{i.title}}</span>
      </div>
    </div>
    <text-canvas class="canvas"></text-canvas>
  </div>
</template>

<script>
import anime from "animejs/lib/anime.es.js";
import TextCanvas from "./common/TextCanvas";
export default {
  components: {
    "text-canvas": TextCanvas
  },
  data() {
    return {
      navList: [
        {
          title: "‰∏™‰∫∫‰ø°ÊÅØ",
          name: "PersonalInformation"
        },
        {
          title: "È°πÁõÆÁªèÂéÜ",
          name: "ProjectExperience"
        },
        {
          title: "Â∑•‰ΩúÁªèÈ™å",
          name: "WorkExperience"
        },
        {
          title: "ÊäÄËÉΩÊ†áÁ≠æ",
          name: "SkillLabel"
        },
        {
          title: "ÂÖ¥Ë∂£Áà±Â•Ω",
          name: "HobbiesInterests"
        }
      ]
    };
  },
  mounted() {
    let that = this;
    let animation = anime.timeline({
      targets: ".tab_nav",
      loop: 1
    });
    animation
      .add({
        duration: 0,
        rotate: function(el, i, l) {
          return i * (360 / that.navList.length);
        },
        translateX: function(el, i, l) {
          return anime.random(-40, 40);
        },
        scale: 0,
        complete: function() {
          anime({
            targets: ".tab_nav .nav",
            rotate: function(el, i, l) {
              //   console.log(-i * (360 / that.navList.length));
              return -i * (360 / that.navList.length);
            }
          });
        }
      })
      .add({
        scale: 1,
        opacity: function(el, i, l) {
          return Math.random() + 0.4;
        },
        delay: function(el, i, l) {
          return i * 100;
        }
      });
  },
  methods: {
    skip(name) {
      this.$router.push({
        name: name
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #f1f1f1;
  overflow: hidden;
}
.canvas {
  position: absolute;
  left: 0;
  top: 0;
}
.user {
  width: 6rem;
  height: 6rem;
  position: relative;
  z-index: 9;
  display: flex;
  justify-content: center;
  align-items: center;
  &::after {
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0.1;
    animation: shadow 1s linear infinite alternate;
  }
  .user_img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    z-index: 5;
    position: absolute;
    left: 0;
    top: 0;
    overflow: hidden;
    img {
      width: 100%;
    }
  }
  .tab_nav {
    opacity: 0;
    transform-origin: 150px 50%;
    left: -100px;
    width: 3rem;
    height: 3rem;
    position: absolute;
    background: var(--base);
    color: white;
    &:active {
      opacity: 0.3 !important;
      box-shadow: 0 0 5px var(--base);
    }
    &:nth-child(2n + 1) {
      border-radius: 50%;
    }
    .nav {
      font-size: 0.75rem;
      padding: 0 0.75rem;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      &::after {
        content: "";
        width: 100%;
        height: 100%;
        border-radius: 50%;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0.2;
        box-shadow: 1px 1px 2px var(--base);
      }
    }
  }
}
@keyframes shadow {
  100% {
    box-shadow: 0px 0px 50px 30px var(--base);
  }
}
</style>


